$darkThemeColors: (
        textColor: #f5f5f5,
        headerNameColor: #fff,
        secondTextColor: #aaa,
        thirdTextColor: #d5d5d5,
        projTitlesColor: #e5e5e5,
        footerLinksColor: #ddd,
        backColor: #222,
        footerBackColor: #252525,
        headerLineColor: #171717,
        secondColor: #aaa,
        thirdColor: #d5d5d5,
        lineColorMain: #353535,
        lineColor: #007ced,
        photoBorderColor: #444
);
